*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.App{
  width: 100%;
  min-height: 100vh; 
  background-image: url(/react-weather-app/static/media/weather_bg-img.c04b9438.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  object-fit: cover;
  object-position: bottom;
}


.cityInput{
  width: 40%;
  height: 50px;
  padding: 0px 10px;
  margin-top: 6rem;
  border: 2px solid white;
  outline-style: none;
  border-radius: 30px;
  font-size: 1.3rem;
}

#city-div{
  padding: 1rem 2rem;
  color: whitesmoke;
}
#city-div p{
  font-size: 1.4rem;
  font-family: sans-serif;
}

#weather-container{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.weather-box{
  width:45%;
  height: 200px;
  margin: 0% 2%;
}

#today-weather{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}
#today-weather img{
  padding-left: 0px;
  margin-right: 2rem;
}
#today-weather span{
  padding-left: 0px;
  margin-right: 2rem;
  color: white;
  font-size:5rem;
}
#today-temp h1{
  color:white;
  font-size: 4rem;
  font-family: Montserrat,sans-serif;
}
#today-temp p{
  color:white;
  font-size: 1.2rem;
  font-family: Montserrat,sans-serif;
  text-transform: capitalize;
}
.daily-data{
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 10px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  align-self: flex-start;
}
.daily-data>div{
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color:white;
  font-family: Montserrat,sans-serif;
}
.daily-data>div span{
  font-size:1.2rem;
}
@media screen and (max-width:576px){
  .cityInput{
    width: 70%;
    height: 40px;
    font-size: 1rem;
  }
  .weather-box{
    width: 100%;
  }
}

